#falling-leaves {
  position: relative;
  width: 100%;
  height: 100%;
}

.leaf {
  position: absolute;
  width: 50px;
  height: 50px;
  background-color: green;
  border-radius: 50%;
  opacity: 0.5;
  animation: falling 5s infinite;
}

@keyframes falling {
  0% {
    top: 0;
    left: 0;
  }

  100% {
    bottom: 0;
    left: 0;
  }
}
.bodyBack {
        
  height: 100vh;
  background-color: #f2f2f2;
  background-image: url("imgs/background.png");
  background-size: cover; /* optional */
  background-position: center;
  background-repeat: no-repeat;
  
} 


/* Set up the container for the leaves */
.leaves-container {
  /*position: relative;*/
  height: 100vh;
  overflow: hidden;
}

/* Style for the individual leaves */
.leaf {
  position: absolute;
  width: 30px;
  height: 30px;
  background-color: #9ac23c00;
  background-image: url("imgs/leave2.png");
  border-radius: 50%;
  animation: falling 8s ease-in-out infinite;
}
.leaf1 {
  position: fixed;
  width: 30px;
  height: 30px;
  background-color: #9ac23c00;
  background-image: url("imgs/leave1.png");
  border-radius: 50%;
  animation: falling 8s ease-in-out infinite;
}

/* Keyframe animation for the falling leaves */
@keyframes falling {
  0% {
    transform: translateY(-10%);
  }
  100% {
    transform: translateY(3220%);
  }
}

/* Position each leaf randomly within the container */
.leaf:nth-child(1) {
  left: 5%;
  top: -5%;
  animation-delay: 0.5s;
}
.leaf:nth-child(2) {
  left: 25%;
  top: -10%;
  animation-delay: 2s;
}
.leaf:nth-child(3) {
  left: 45%;
  top: -15%;
  animation-delay: 3.5s;
}
.leaf:nth-child(4) {
  left: 65%;
  top: -20%;
  animation-delay: 1s;
}
.leaf:nth-child(5) {
  left: 85%;
  top: -25%;
  animation-delay: 2.5s;
}
.leaf:nth-child(6) {
  left: 95%;
  top: -15%;
  animation-delay: 2.5s;
}

.leaf:nth-child(7) {
  left: 55%;
  top: -15%;
  animation-delay: 1.5s;
}

.leaf:nth-child(8) {
  left: 20%;
  top: -15%;
  animation-delay: 1.0s;
}

