* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Center the content */


/* Style the login box */
.login-box {
  position: relative;
  top: -70%;
  left: 38%;
  max-width: 400px;
  width: 100%;
  padding: 20px;
  border-radius: 10px;
  background-color:rgba(255, 255, 255, 0.26);
  backdrop-filter: blur(10px);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  z-index: -5;
  text-align: center;
}

/* Style the form elements */
label {
  display: block;
  margin-bottom: 2px;
  font-weight: bold;
  
}

input[type="text"],
input[type="password"] {
  width: 90%;
  height: 45px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 15px;
  font-family: 'Poppins', sans-serif;
}

input[type="submit"] {
  background-color: #FFA15F;
  font-family: 'Poppins', sans-serif;
  
  width: 90%;
  height: 45px;;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

input[type="submit"]:hover {
  background-color: #45a049;
}

/* Responsive styles */
@media screen and (max-width: 600px) {
  .login-box {
    max-width: 300px;
  }
}
